import { getArtist } from './index';
export const playMode = {
  sequence: 0,
  loop: 1,
  random: 2,
};

export class Song {
  constructor(options) {
    this.id = options.id || ''; //id
    this.name = options.name || ''; // 
    this.picUrl = options.picUrl || ''; // 
    this.album = options.album || ''; // 
    this.alia = options.alia || ''; // 
    this.artists = getArtist(options.artists || []).join('') || ''; // 
    this.privilege = options.privilege || {}; // 
  }
}
